import React, { Children } from "react";
import { Link } from "gatsby";
import styled from 'styled-components';

const Wrapper = styled.div`
  background: ${props => props.bg};
  border-radius: 8px;
  box-shadow: 0px 8.71336px 58.089px rgba(0, 0, 0, 0.15);
`;


const BottomCta = ({bgColor, children}) => (
  <>
    <section id='bottom-cta' className='center'>
      <div className='row my-5 justify-content-center align-items-center w-100'>
          <Wrapper className='container elevated text-center mx-2 my-5 py-5' bg={bgColor}>
            <div className='px-md-5 w-100'>
              {children}
            </div>
          </Wrapper>
      </div>
    </section>
  </>
)

export default BottomCta;